import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import useMediaQuery from '@mui/material/useMediaQuery';
import MemoryItemLoading from 'pages/memories/MemoryItemLoading';
import MemoryGrid from 'pages/memories/MemoryGrid';
import GridView from './GridView';
import api from '../Api';
import CreateMemoryItem from './CreateMemoryItem';

async function getMemories(
  handleMemoryCallback,
  handleTokenCallback,
  handleThumbnailCallback,
  nextToken = '',
) {
  try {
    const resp = await api.getMemories(nextToken);
    if (resp) {
      if (resp.memories) {
        handleThumbnailCallback(resp.memories);
        const thumbnails = await Promise.all(
          api.getThumbnails(resp.memories),
        );
        handleMemoryCallback(thumbnails);
        handleTokenCallback(resp.next_continuation_token || '');
      }
    }
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

export default function OwnMemories() {
  const [nextToken, setNextToken] = useState('');
  const [memoryIDs, setMemoryIDs] = useState([]);
  const [memories, setMemories] = useState([]);
  const [count, setCount] = useState(0);
  const [grid, setGrid] = useState(null);
  const navigate = useNavigate();
  const aboveSM = useMediaQuery((theme) => theme.breakpoints.up('sm'));

  const handleClick = (memDetails) => {
    navigate(`/memory/${memDetails.memory_id}`, { state: memDetails });
  };

  const picLoaded = () => setCount((cnt) => cnt + 1);
  const handleTokenChange = (newToken) => setNextToken(newToken);
  const addMemories = (details) => setMemories((arr) => arr.concat(details));
  const addMemoryIDs = (ids) => setMemoryIDs((arr) => arr.concat(ids));

  useEffect(() => getMemories(addMemories, handleTokenChange, addMemoryIDs, nextToken), []);

  function GetThumbnailGrid() {
    console.log('above sm', aboveSM);
    const thumbnailGrid = (
      <GridView
        create={aboveSM}
        memories={memories}
        nextToken={nextToken}
        handleMoreClick={() => getMemories(
          addMemories,
          handleTokenChange,
          addMemoryIDs,
          nextToken,
        )}
        handleImageLoad={picLoaded}
        handleClick={handleClick}
      />
    );
    setGrid(thumbnailGrid);
  }

  useEffect(() => GetThumbnailGrid(), [memories, nextToken, aboveSM]);

  console.log('I am in own memory', memories);

  return (memories.length === memoryIDs.length) && (count >= memoryIDs.length) ? (
    grid
  ) : (
    <>
      <MemoryGrid>
        {aboveSM ? <CreateMemoryItem /> : null}
        {memoryIDs.map((memory) => <MemoryItemLoading memory={memory} />)}
      </MemoryGrid>

      <Box component="div" sx={{ display: 'none' }}>
        {grid}
      </Box>
    </>
  );
}
