import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import ActionButton from 'components/ActionButton';
import OwnerInfo from 'components/OwnerInfo';
import SimpleSnackbar from 'components/simple_snackbar';
import Skeleton from '@mui/material/Skeleton';
import imageLoading from 'static/images/image-loading.png';
import commonUtils from 'utils';
import NoMemories from 'components/NoMemories';
import ReviewItem from 'components/ReviewItem';

import api from 'pages/Api';

const getThumbnailSrcSet = (thumbnailImage) => {
  const srcSetList = thumbnailImage.images ? thumbnailImage.images.map(({ src, width }) => `${src} ${width}w`) : [];
  return srcSetList.join(',');
};

async function getReviewMemory(
  handleMemoryCallback,
  handleThumbnailCallback,
  memoryID = '',
) {
  try {
    const resp = await api.getPendingMemories(memoryID);
    if (resp) {
      if (resp.memory) {
        handleThumbnailCallback(resp.memory);
        /* eslint-disable no-console */
        console.log('review memories', resp);
        const thumbnail = await api.getPendingThumbnails(resp.memory);
        handleMemoryCallback(thumbnail);
      }
    }
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

async function acceptMemory(memoryID, handleAcceptCallback) {
  try {
    const userInfo = commonUtils.getUserInfo();
    const resp = await api.acceptShare(memoryID, userInfo.email);
    console.log(resp);
    handleAcceptCallback(resp);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

async function rejectMemory(memoryID, handleRejectCallback) {
  try {
    const userInfo = commonUtils.getUserInfo();
    const resp = await api.rejectShare(memoryID, userInfo.email);
    console.log(resp);
    handleRejectCallback(resp);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

export default function ReviewMemories({ memoryID }) {
  const [loaded, setLoaded] = useState(false);
  const [memID, setMemID] = useState(null);
  const [pendingMemory, setPendingMemory] = useState([]);
  const [grid, setGrid] = useState(null);

  const [snack, setSnack] = useState(null);
  const [snackMessage, setSnackMessage] = useState(null);
  const [snackError, setSnackError] = useState(null);

  console.log(memoryID);
  const handleSnackMessage = (msg, error) => {
    setSnack(true);
    setSnackMessage(msg);
    setSnackError(error);
  };

  const handleSnackBarClose = () => {
    setSnack(false);
    setSnackMessage(null);
    setSnackError(null);
  };

  const handleMemoryRemove = () => getReviewMemory(setPendingMemory, setMemID);
  useEffect(() => getReviewMemory(setPendingMemory, setMemID, memoryID), []);

  const handleAcceptActionResults = (resp) => {
    console.log('accept response', resp);
    if (resp.ok) {
      handleSnackMessage('Memory share accepted', false);
      setLoaded(false);
      handleMemoryRemove();
    } else {
      handleSnackMessage('Error while accepting memory share', true);
    }
  };

  const handleRejectActionResults = (resp) => {
    console.log('reject response', resp);
    if (resp.ok) {
      handleSnackMessage('Memory share rejected', false);
      setLoaded(false);
      handleMemoryRemove();
    } else {
      handleSnackMessage('Error while rejecting memory share', true);
    }
  };

  const handleAccept = () => {
    acceptMemory(pendingMemory.thumbnail.memory_id, handleAcceptActionResults);
    console.log('accepting memoryID', pendingMemory.thumbnail.memory_id);
  };

  const handleReject = () => {
    rejectMemory(pendingMemory.thumbnail.memory_id, handleRejectActionResults);
    console.log('rejecting memoryID', pendingMemory.thumbnail.memory_id);
  };

  const handleImageLoad = () => setLoaded(true);

  /* eslint-disable no-console */
  console.log('pending memory', pendingMemory);

  function ReviewCarousel() {
    if (pendingMemory) {
      const rc = (
        <Box
          sx={{
            display: 'flex',
            width: '100%',
            backgroundColor: 'transparent',
            backgroundSize: 'cover',
            backgroundImage: 'linear-gradient(white, #0e516a)',
            justifyContent: 'center',
          }}
        >
          <Stack
            direction="column"
            alignItems="center"
            sx={{ width: '100%', my: 3 }}
          >
            <OwnerInfo
              name={pendingMemory.owner_details.name || ''}
              email={pendingMemory.owner_details.email || 'P'}
              picture={pendingMemory.owner_details.picture || ''}
            />
            <Typography sx={{ mb: 2 }}>has shared a memory with you</Typography>
            <ReviewItem
              title={pendingMemory.thumbnail.title}
              date={pendingMemory.thumbnail.date}
              thumbnailImage={pendingMemory.thumbnail.photo.src}
              thumbnailImageSrcSet={getThumbnailSrcSet(pendingMemory.thumbnail.photo)}
              handleImageLoad={handleImageLoad}
              handleClick={() => {}}
            />
            <Stack direction="row" sx={{ m: 1 }}>
              <ActionButton label="Accept" color="primary" handleClick={handleAccept} />
              <ActionButton label="Reject" color="error" handleClick={handleReject} />
            </Stack>
          </Stack>
          <SimpleSnackbar
            snackBarOpen={snack}
            message={snackMessage}
            onClose={handleSnackBarClose}
            error={snackError}
          />
        </Box>
      );
      setGrid(rc);
    } else {
      setGrid(<NoMemories message="Nothing to review right now." />);
    }
  }

  useEffect(() => ReviewCarousel(), [pendingMemory, memoryID]);

  console.log(pendingMemory, memID, loaded, pendingMemory && loaded);
  return (pendingMemory && loaded) ? (
    grid
  ) : (
    <>
      <Box
        sx={{
          display: 'flex',
          width: '100%',
          backgroundColor: 'transparent',
          backgroundSize: 'cover',
          backgroundImage: 'linear-gradient(white, #0e516a)',
          justifyContent: 'center',
        }}
      >
        <Stack
          direction="column"
          alignItems="center"
          sx={{ width: '100%', my: 3 }}
        >
          <OwnerInfo
            name={<Skeleton type="rectangular" sx={{ width: '15%' }} />}
            email="P"
            picture=""
          />
          <Typography sx={{ mb: 2 }}>has shared a memory with you</Typography>
          <ReviewItem
            title=""
            date=""
            thumbnailImage={imageLoading}
            thumbnailImageSrcSet=""
            handleImageLoad={() => {}}
            handleClick={() => {}}
          />
          <Stack direction="row" sx={{ m: 1 }}>
            <ActionButton label="Accept" color="primary" handleClick={handleAccept} />
            <ActionButton label="Reject" color="error" handleClick={handleReject} />
          </Stack>
        </Stack>
      </Box>
      <Box component="div" sx={{ display: 'none' }}>
        {grid}
      </Box>
    </>
  );
}

ReviewMemories.defaultProps = {
  memoryID: '',
};

ReviewMemories.propTypes = {
  memoryID: PropTypes.string,
};
